export default function minOperations(logs) {
    console.log("dynamic loaded and executed")
    const result = [];
    const stack = [];
    //for (const log of logs) {
    for (let i = 0; i < logs.length; i++) {
        const log = logs[i];
        if (log === "../") {
            if (stack.length > 0) {
                const p = stack.pop();
                result.push({ action: "POP", params: [], j:i, description: `pop '${p}'` }); //'${JSON.stringify(logs)}'
            }
        } else if (log === "./") {
            result.push({ action: "c", params: [log], j:i, description: `ignore '${log}'` });
        } else {
            stack.push(log);
            result.push({ action: "PUSH", params: [log], j:i, description: `push '${log}'` });
        }
    }
    return result;
}
